import React, { Component } from 'react';
import { get, getRequest, getTourAuthKey, isNullOrEmpty } from 'utils';
import TourItem from './TourItem';
import { connect } from 'react-redux';
import * as action from 'action';
import { blankDiv } from 'utils';
import './Tour.css';
import $ from 'jquery';
import TourCategoryItem from './TourCategoryItem';

const uri = 'tour/areaBasedList';

class Tour extends Component {
    constructor(props){
    super(props);

    if (this.props.history != null) {
      this.props.setHistory(this.props.history);
      $('#dv-body-back.gradient-1').css('opacity', 0);
      $('#dv-body-back.gradient-2').css('opacity', 0);
      $('#dv-body-back.gradient-3').css('opacity', 1);
    }

    this.category = [
      { value: '0', label: '평창휴캉스', icon: 'home'},
      { value: '1', label: '산/계곡', icon: 'filter_hdr'},
      { value: '2', label: '관광지', icon: 'account_balance'},
      { value: '3', label: '둘레길', icon: 'nature_people'},
      { value: '4', label: '레저/스포츠', icon: 'golf_course'},
    ];

    this.state = {
    }
  }

  componentDidMount() {
    if (!this.props.tourData || this.props.tourData.length < 1) {
      this.requestList();
    }

  }

  requestList(params) {
    var data = {
      ServiceKey: getTourAuthKey(),
      //contentTypeId: 12,
      areaCode: 32,
      sigunguCode: 15,
      listYN: 'Y',
      MobileOS: window.os,
      MobileApp: window.appName,
      arrange: 'A',
      numOfRows: 9999,
      pageNo: 1,
      _type: 'json',
    }
    
    console.log('requestList');
    get(uri, data, 
      (res) => {
        console.log(res);
        this.props.setTourData(res.response.body.items.item);
      }, 
      (res) => {
        console.log(res);
      });
  }

  renderCategory() {
    var list = [];
    this.category.forEach(item => {
      list.push(<TourCategoryItem key={item.value} data={item} />);
    })
    return(<div>
      {list}
    </div>);
  }

  renderList() {
    if (this.props.tourData && this.props.tourData.length > 0) {
      var list = [];
      var count = 0;
      this.props.tourData.forEach(item => {
        if (!isNullOrEmpty(item.firstimage))
          item.firstimage = item.firstimage.replace('http://', 'https://');
        if (!isNullOrEmpty(item.firstimage2))
          item.firstimage2 = item.firstimage2.replace('http://', 'https://');

        if(!isNullOrEmpty(item.firstimage) && !isNullOrEmpty(item.addr1))
          list.push(<TourItem key={count++} data={item} />);
      })
      return(<div>
        {list}
      </div>);
    }
    return(<div>
    </div>);
  }

  render(){
    return (
      <div id='dv-tour'>
        { blankDiv('60px') }
        <div id='dv-tour-list'>
          <div className='contents'>
            {this.renderCategory()}
          </div>
        </div>
      </div>);
  }
  /**
   * 
   * navigator.geolocation.getCurrentPosition((position)=>{alert('Lat: ' + position.coords.latitude + ' ' + 'Lon: ' + position.coords.latitude);});
  */
}


const mapStateToProps = (state) => {
  return {
    tourData: state.tourData
      //user: state.auth.user
  };
}

const mapDispatchToProps = (dispatch) => {
  return {
    setHistory: (history) => { dispatch(action.setHistory(history)) },
    setTourData: (tourData) => { dispatch(action.setTourData(tourData)) }
      //setPage: (page, header, footer) => { dispatch(action.setPage(page, header, footer)) }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Tour);